/**
 * TicketNetwork model events
 */

'use strict';

import {EventEmitter} from 'events';
import TicketNetwork from './ticketNetwork.model';
var TicketNetworkEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
TicketNetworkEvents.setMaxListeners(0);

// Model events
var events = {
  save: 'save',
  remove: 'remove'
};

// Register the event emitter to the model events
for(var e in events) {
  let event = events[e];
  TicketNetwork.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    TicketNetworkEvents.emit(event + ':' + doc._id, doc);
    TicketNetworkEvents.emit(event, doc);
  };
}

export default TicketNetworkEvents;
